@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#root {
    min-height: 100vh;
    background-color: aliceblue;
    position: relative;
    font-family: "Poppins", sans-serif;
}

.main {
    width: 100%;
    height: calc(100vh - 80px);
    background: transparent;
    overflow: hidden;
}

.main-container {
    width: 80%;
    /* background: tomato; */
    height: 100%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
}

.editor {
    height: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.preview {
    height: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.main-child {
    margin: 0 4px;
    transition: 0.5s ease;
    background: white;
}

/* text area */
#editor {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    resize: none;
    padding: 20px;
    font-size: 1.2rem;
    overflow-y: scroll;
}
/* end text area */

#preview {
    width: 100%;
    height: 100%;
    padding: 24px;
    font-size: 1.2rem;
    word-wrap: break-word;
    overflow-y: scroll;
}

button {
    font-family: "Poppins", sans-serif;
}

.navbar {
    width: 100%;
    position: relative;
    z-index: 10;
    height: 80px;
    background-color: rgb(223, 239, 255);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}

.nav-container {
    width: 80%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.logo {
    font-size: 2rem;
}

.buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.button {
    outline: none;
    border: none;
    padding: 10px 17px;
    min-width: 120px;
    background: aliceblue;
    border-radius: 5px;
    font-size: 0.9rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.button:nth-child(2) {
    margin-left: 10px;
}

.button:hover {
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}

.button-icon {
    margin-left: 10px;
    transition: 0.3s ease;
}
.button-icon:hover {
    font-size: 1rem;
}

